// Colours
$color-background: #ffffff;
$color-foreground: #000000;
$color-hover: #c7efcf;
$color-dark: #0C090D;

$intro-color-background: #333745;
$intro-color-foreground: #edf7ff;
$intro-color-hover: #c7efcf;
$intro-color-dark: #5C6B73;
@import '../common/variables';

html, body {
    height: "100%";
}

.resume {
    margin: 5vh auto;
    max-width: 800px;

    @media screen and (max-width: 1000px) {
        margin: 5vh 15vw;
    }
}

a {
    text-decoration: none;
    color:unset;
}


.nav--mini {
    margin-top: 5vh;

    .btn--nav {
        color: $intro-color-foreground;
        background-color: $intro-color-dark;

        border-radius: 0;
        padding-right: 10px;
        padding-left: 20px;

        &:first-of-type {
            border-radius: 5px 0 0 5px;
        }

        &:last-of-type {
            border-radius: 0 5px 5px 0;
        }

        &:hover {
            color: $intro-color-foreground;
        }

        @media screen and (max-width: $break-medium) {
            font-size: .85rem;
            padding: 10px;
        }
    }
}